.Buttons {
    width: 100%;
    display: flex;
    justify-content: center;
    padding-top: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid black;
  }
  
  .Buttons button {
    font-size: 1.5rem;
    background: none;
    outline: none;
    border: 1px solid black;
    cursor: pointer;
  }
  
  .Buttons button:hover {
    background: black;
    color: white;
  }
  
  .Buttons button + button {
    margin-left: 1rem;
  }